@import url("https://fonts.googleapis.com/css2?family=Lobster&family=Poppins&family=Righteous&family=Rowdies:wght@300&display=swap");
/*fonts
font-family: 'Lobster', cursive;
font-family: 'Poppins', sans-serif;
font-family: 'Righteous', cursive;
font-family: 'Rowdies', cursive
*/
/*general styling*/
* {
  margin: 0;
  padding: 0;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
}
a {
  /*remove styles from anchor tags*/
  text-decoration: none;
  color: #fff;
  font-family: "Ropa Sans" sans-serif sans-serif sans-serif;
  text-shadow: 1px 1px 1px #000;
}
h1 {
  font-family: "Poppins", sans-serif;
}
button:hover {
  cursor: pointer;
}
#root {
  background-color: rgb(22, 21, 21);
  color: white;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  position: relative;
}

/*nav bar*/
.nav {
  padding: 1.2rem;
  position: relative;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  gap: 5rem;
  font-family: "Righteous", cursive;
}
.nav img {
  /*cart icon*/
  width: 30px;
  height: auto;
}
.nav button {
  /*remove the button styling*/
  background: none;
  outline: none;
  border: none;
  /*to make the number of elements
  positioned relative to the icon*/
  position: relative;
}

.nav > div {
  /*to separate logo and other content*/
  height: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 1.2rem;
}
.cart {
  position: absolute;
  height: 100%;
  top: 0;
  right: 0;
  width: 0px;
  background-color: white;
  color: black;
  z-index: 1;
  transition: all 0.4s;
  overflow-x: hidden;
  overflow-y: auto;
}
.cart::-webkit-scrollbar {
  /*hide the scroll bar*/
  display: none;
}
.slide {
  /*when the cart opens*/
  padding: 1rem;
  width: 400px;
  transition: all 0.4s;
  overflow-y: scroll;
}
.cartElement {
  display: flex;
  align-items: center;
  width: 100%;
}
.cartElement > img {
  width: 30%;
  height: 30%;
}
.cartElement div {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 0.3rem;
}
.cartElement .cartNumbers {
  flex-direction: row;
  gap: 1rem;
}
.cartNumbers > button {
  width: 32px;
  height: 32px;
  background-color: black;
  color: white;
  font-size: large;
  border: 3px solid gold;
  border-radius: 0.5rem;
}

.cartElement > div:first-of-type {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
}

.cart .cartFooter {
  margin-top: 2rem;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  gap: 0.75rem;
}
.cartFooter > button {
  width: 40%;
  height: 2.5rem;
  font-family: "Righteous", cursive;
  font-size: large;
  border: 1.25px solid black;
  border-radius: 0.25rem;
}
.cartFooter > button:first-of-type {
  /*targets the checkout button*/
  width: 60%;
  background-color: gold;
}
.links {
  /*for the navLink component*/
  font-size: large;
  color: white;
}
/*home*/
.home {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.hero {
  background-image: url("./Components/images/background.jpg");
  background-size: cover;
  background-position: 50%;
  height: 80vh;
  display: flex;
}
.heroText {
  padding: 1rem;
  width: 60%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.transparent {
  background-color: rgba(0, 0, 0, 0.8);
  display: flex;
  justify-content: center;
}
.footer {
  display: flex;
  width: 60%;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 1.2rem;
}
#reviewCardsHolder {
  min-height: 40vh;
  background-color: white;
  padding: 1rem;
  display: flex;
  width: 100%;
  border: 2px solid black;
  border-radius: 0.5rem;
  font-family: "Rowdies", cursive;
}
.reviewCard {
  color: black;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 50%;
}
.reviewCard > img {
  width: 60px;
  height: auto;
  border-radius: 2rem;
}
#reviewCardsHolder div:first-child {
  /*to add a border between reviews*/
  border-right: 2px solid black;
}
/*Shop*/
.shop {
  display: grid;
  grid-template: 6fr 1fr / 1fr;
  place-items: center;
}
.shopItemsHolder {
  display: grid;
  grid-template: repeat(3, 300px) / repeat(3, 300px);
}
.pageNumbers > button {
  width: 30px;
  height: 30px;
  background-color: white;
  color: black;
  font-size: large;
  border: 3px solid gold;
  margin-left: 1rem;
  font-family: "Righteous", cursive;
}
.card {
  width: 275px;
  height: 275px;
  background-size: contain;
  background-repeat: no-repeat;
  border: 2px solid white;
  background-color: #000;
  border-radius: 1rem;
  box-shadow: 6px 6px 0 white;
  display: flex;
  flex-direction: column;
  padding: 0.5rem;
  align-items: center;
  position: relative;
}
.card:hover {
  transition: all 0.4s;
  transform: scale(1.05);
  cursor: pointer;
}
.card > img {
  width: 60%;
  height: 60%;
}
.cardOverlay {
  position: absolute;
  bottom: 0px;
  left: 0px;
  color: white;
  background-color: rgb(0, 0, 0);
  height: 0%;
  width: 100%;
  border-radius: 1rem;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  visibility: hidden;
}
.card:hover .cardOverlay {
  transition: all 0.4s;
  height: 100%;
  visibility: visible;
}
.card button {
  background: none;
  outline: none;
  font-size: large;
  color: white;
}
